/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  font-family: '微软雅黑', 'Microsoft YaHei', Arial;
  font-size: 62.5%;
  line-height: 1.6;
  color: #000;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
:active,
:focus {
  outline: none;
}
a {
  text-decoration: none;
  color: #000;
}
img,
input {
  border: none;
}
input {
  margin: 0;
  padding: 0;
}
button,
input[type=button],
input[type=submit],
input[type=reset],
select {
  cursor: pointer;
  text-align: center;
  text-indent: 0;
  background: no-repeat;
  padding: 0;
  margin: 0;
  border: none;
}
html,
body {
  font-size: 62.5%;
}
body {
  padding: 1rem;
}
#__dir > div.row {
  position: relative;
  z-index: 100;
}
#__dirList li {
  cursor: pointer;
  opacity: .8;
  transition: all .3s;
  font-size: 1.6rem;
}
#__dirList li .icons {
  height: 2rem;
  width: 2rem;
  line-height: 2rem;
  font-size: 1.6rem;
}
#__dirList li:hover {
  opacity: 1;
}
.loading {
  text-align: center;
  animation: rotate 1s infinite;
}
.loading span {
  font-size: 3rem;
}
.history {
  position: absolute;
  width: 100%;
  left: 0;
  top: 100%;
  overflow: hidden;
  transition: all .3s;
  height: 0;
  z-index: 100;
}
.history > div {
  padding: 0 1.5rem;
}
.history .list-group {
  margin-bottom: 0;
  width: 100%;
}
.history .list-group li {
  cursor: pointer;
  font-size: 1.4rem;
}
.history .list-group li a {
  display: block;
  width: 100%;
  height: 100%;
  text-decoration: none;
}
.history.active {
  height: 20rem;
  overflow-y: auto;
}
